@import "common/font";
@import "lib/normilize";
@import "lib/prism";
@import "common/constants";
@import "common/common";
@import "common/navigation";
@import "common/header";
@import "common/footer";
@import "home/home";
@import "entries/entry_list";
@import "entries/entry";


* {
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: border-box;
}

html, body {
  font-family: 'Futura PT',-apple-system,'BlinkMacSystemFont','Segoe UI','Roboto','Oxygen','Ubuntu','Cantarell','Fira Sans','Droid Sans','Helvetica Neue','Arial',sans-serif;
  background-color: $dark-primary-color;
  color: $dark-text-color;
  position: relative;
  height: 100%;
  //transition: color 0.5s;
  input[type=submit] {
    color: $dark-text-color;
  }
}

.main-container {
  min-height: 100vh;
  overflow: hidden;
  display: block;
  position: relative;
}

.flex-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.max-width-wrapper {
  max-width: 800px;
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 0 8px 50px 8px;
  img:hover {
    cursor: pointer;
  }
}

.flex-center {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#theme-switcher {
  position: fixed;
  bottom: 12px;
  right: 12px;
  width: 30px;
  height: 30px;
  background-image: url('/assets/images/moon.svg');
  background-position: center;
  background-repeat: no-repeat;
  transition: all 0.4s;
  &:hover {
    cursor: pointer;
  }
}

.light-mode {
  transition: all 0.5s;
  background-color: $light-primary-color !important;
  body {
    background-color: $light-primary-color !important;
    color: $light-text-color !important;
  }

  a {
    color: $light-text-color;
  }

  input[type=submit] {
    color: $light-text-color;
  }

  .main-header {
    background-color: $light-secondary-color;
  }

  .main-footer {
    background-color: $light-secondary-color;
  }
}

.about-me {
  p {
    margin: 8px 0 8px 0;
    width: 100%;
    text-align: center;
  }
}

.me {
  width: 150px;
  height: 150px;
  border-radius: 75px;
}

.work {
  h1 {
    margin-top: 30px;
    text-align: left;
  }

}

.job-item {
  img {
    display: block;
    width: 100%;
    max-width: 500px;
    margin: auto;
    border-radius: 4px;
  }
  &:last-child {
    margin-bottom: 50px;
  }
}

.not-found {
  text-align: center;
  margin: 32px 8px 32px 8px;
  min-height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.courses {
  padding-bottom: 30px;
}

.highlighter-rouge {
  font-size: 16px;
}
